import React, {useState} from 'react';
import {connect} from 'react-redux';
import {Switch, Route} from 'react-router-dom';
import Forgot from './components/Auth/Forgot';
import Login from './components/Auth/Login';
import MailNotConfirmed from "./components/Auth/MailNotConfirmed";
import Register from './components/Auth/Register';
import CaseChallenge from './components/CaseChallenge';
import Cases from './components/Cases';
import Main from './components/Main';
import Materials from './components/Materials';
import Buddies from './components/Buddies';
import PrivateAccount from './components/PrivateAccount';
import User from './components/User';
import MainLayout from './utils/layout/main';
import NotFound from './utils/service';

const App = ({isAuth}) => {
  const [isMenuShow, setIsMenuShow] = useState(false)

  const onClick = e => {
    e.target.classList.length ?
      (e.target.classList[0].includes('user') && !isMenuShow ? setIsMenuShow(true) : setIsMenuShow(false))
      : setIsMenuShow(false)
  }

  return (
    <div className='app' onClick={onClick}>
      <Switch>

        <Route path='/' exact render={() =>
          <MainLayout isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}>
            <Main/>
          </MainLayout>}/>

        <Route path='/cases' exact render={() =>
          <MainLayout isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}>
            <div className='container'><Cases/></div>
          </MainLayout>}/>

        <Route path='/case-challenge' exact render={() =>
          <MainLayout isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}>
            <div className='container'><CaseChallenge/></div>
          </MainLayout>}/>

        <Route path='/materials' render={() =>
          <MainLayout isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}>
            <div className='container'><Materials/></div>
          </MainLayout>}/>

        <Route path='/buddies' render={() =>
          <MainLayout isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}>
            <div className='container'><Buddies/></div>
          </MainLayout>}/>

        <Route path='/user' render={() =>
          <MainLayout isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}>
            <div className='container'><User/></div>
          </MainLayout>}/>

          <ForAuth isAuth={isAuth} isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}/>

        <Route render={() => <NotFound/>}/>
      </Switch>
    </div>
  );
}

const mstp = ({user}) => ({isAuth: user.isAuth})

export default connect(mstp)(App)

const ForAuth = ({isAuth, isMenuShow, setIsMenuShow}) => (
  <>
    {isAuth
      ? <Route path='/account' render={() =>
        <MainLayout isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}>
          <div className='container'><PrivateAccount/></div>
        </MainLayout>}/>
      : <>
        <Route path='/login' render={() => <Login isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}/>}/>
        <Route path='/forgot' render={() => <Forgot isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}/>}/>
        <Route path='/mail-not-confirmed' render={() => <MailNotConfirmed isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}/>}/>
        <Route path='/register' render={() => <Register isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}/>}/>
      </>
    }
  </>
)
